import React, { useState, useEffect } from "react";
import { ref, push, onValue } from "firebase/database";
import { dbRealtime } from "./firebaseConfig"; // Firebase  
import "./Orders.css"; //   

function Orders() {
  const [customerName, setCustomerName] = useState(""); //   
  const [menuName, setMenuName] = useState("");
  const [quantity, setQuantity] = useState("");
  const [selectedDate, setSelectedDate] = useState(new Date()); //    
  const [reservationDateTime, setReservationDateTime] = useState(""); //    
  const [startDate, setStartDate] = useState(new Date()); //   
  const [orderList, setOrderList] = useState([]); //    

  //  "YYYY-MM-DD"    ( )
  const formatDateToYMD = (date) => {
    const year = date.getFullYear();
    const month = String(date.getMonth() + 1).padStart(2, "0"); //   
    const day = String(date.getDate()).padStart(2, "0");
    return `${year}-${month}-${day}`;
  };

  //        (7)
  const getDisplayedDates = (start) => {
    const dates = [];
    const currentDate = new Date(start); //      
    currentDate.setDate(currentDate.getDate() - currentDate.getDay()); //   

    for (let i = 0; i < 7; i++) {
      dates.push(new Date(currentDate));
      currentDate.setDate(currentDate.getDate() + 1); //   
    }

    return dates;
  };

  //  
  const daysOfWeek = ["", "", "", "", "", "", ""];

  //  "MM/DD"   
  const formatDate = (date) => {
    const day = date.getDate();
    return `${day}`;
  };

  //   "0000 00 00"   
  const formatSelectedDate = (date) => {
    const year = date.getFullYear();
    const month = date.getMonth() + 1; //  0  1 
    const day = date.getDate();
    return `${year} ${month} ${day}`;
  };

  //  "0000 00"   
  const formatMonth = (date) => {
    const year = date.getFullYear();
    const month = date.getMonth() + 1;
    return `${year} ${month}`;
  };

  //      
  const handleDateChange = (direction) => {
    const newStartDate = new Date(startDate);
    newStartDate.setDate(newStartDate.getDate() + direction * 7); // 7 
    setStartDate(newStartDate); //    
  };

  // Firebase     
  useEffect(() => {
    const ordersRef = ref(dbRealtime, "orders");
    onValue(ordersRef, (snapshot) => {
      const data = snapshot.val();
      if (data) {
        const orders = Object.keys(data).map((key) => ({
          id: key,
          ...data[key],
        }));
        setOrderList(orders); //   
      } else {
        setOrderList([]); //     
      }
    });
  }, []);

  // Firebase   
  const handleOrderSubmit = async () => {
    if (customerName && menuName && quantity && reservationDateTime) {
      const reservationDate = formatDateToYMD(new Date(reservationDateTime)); //  

      const newOrder = {
        customerName,
        dateTime: reservationDateTime, //    
        menuName,
        quantity: Number(quantity),
        date: reservationDate, // YYYY-MM-DD   
      };

      try {
        const ordersRef = ref(dbRealtime, "orders"); // Firebase db  
        await push(ordersRef, newOrder); // Realtime Database   
        console.log("Order saved to Firebase Realtime Database");
        setCustomerName("");
        setMenuName("");
        setQuantity("");
        setReservationDateTime("");
      } catch (e) {
        console.error("Error saving order: ", e);
      }
    }
  };

  //       ( )
  const filteredOrders = orderList.filter(
    (order) => order.date === formatDateToYMD(selectedDate)
  );

  return (
    <div>
      <h2>  </h2>

      {/*    */}
      <div className="month-display">
        <h3>{formatMonth(startDate)}</h3>
      </div>

      {/*    */}
      <div className="day-of-week">
        {daysOfWeek.map((day, index) => (
          <div
            key={index}
            className={`day-label ${index === 0 ? "sunday" : ""} ${
              index === 6 ? "saturday" : ""
            }`}
          >
            {day}
          </div>
        ))}
      </div>

      {/*    */}
      <div className="date-tabs-container">
        {/*   */}
        <button className="arrow-button" onClick={() => handleDateChange(-1)}>
          &lt;
        </button>
        <div className="date-tabs">
          {getDisplayedDates(startDate).map((date, index) => (
            <div
              key={index}
              className={`date-tab ${
                selectedDate.toDateString() === date.toDateString()
                  ? "selected"
                  : ""
              }`}
              onClick={() => setSelectedDate(date)}
            >
              {formatDate(date)} {/*   */}
            </div>
          ))}
        </div>
        <button className="arrow-button" onClick={() => handleDateChange(1)}>
          &gt;
        </button>
      </div>
      {/*     */}
      <div className="order-list">
        <h3>{formatSelectedDate(selectedDate)}   </h3>
        {filteredOrders.length > 0 ? (
          <ul>
            {filteredOrders.map((order) => (
              <li key={order.id}>
                {`${order.customerName} - ${order.dateTime} - ${order.menuName} - ${order.quantity}`}
              </li>
            ))}
          </ul>
        ) : (
          <p>   .</p>
        )}
      </div>

      {/*    */}
      <div className="order-input">
        <h3> </h3>
        <input
          type="text"
          value={customerName}
          onChange={(e) => setCustomerName(e.target.value)}
          placeholder=" "
        />
        <input
          type="datetime-local"
          value={reservationDateTime}
          onChange={(e) => setReservationDateTime(e.target.value)}
          placeholder="  "
        />
        <input
          type="text"
          value={menuName}
          onChange={(e) => setMenuName(e.target.value)}
          placeholder="   (: )"
        />
        <input
          type="number"
          value={quantity}
          onChange={(e) => setQuantity(e.target.value)}
          placeholder=" "
        />
        <button onClick={handleOrderSubmit}> </button>
      </div>
    </div>
  );
}

export default Orders;
