import React, { useState } from "react";
import { doc, setDoc } from "firebase/firestore"; // Firestore   
import { dbFirestore } from "./firebaseConfig"; // Firestore  

function Settings() {
  const [loading, setLoading] = useState(false);

  // ""   
  const handleSaveRecipe = async () => {
    setLoading(true);

    const recipe = {
      menuName: "",
      ingredients: {
        chickenLeg: { amount: 1, unit: "" }, //  1
        onion: { amount: 45, unit: "g" }, //  45g
        sauce: { amount: 10, unit: "g" }, //  10g
      },
    };

    try {
      // Firestore "menus"  ""  
      const recipeRef = doc(dbFirestore, "menus", ""); // Firestore  
      await setDoc(recipeRef, recipe); // Firestore  
      console.log("Recipe saved successfully.");
    } catch (e) {
      console.error("Error saving recipe: ", e);
    }
    setLoading(false);
  };

  return (
    <div>
      <h2> </h2>
      <button onClick={handleSaveRecipe} disabled={loading}>
        {loading ? " ..." : " "}
      </button>
    </div>
  );
}

export default Settings;
